.slot-machine {
  text-align: center;
  margin: 20px;

  &__header {
    margin-bottom: 20px;
  }

  &__slots {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    height: 100px;
    position: relative;
  }

  &__slot {
    width: 100px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #007bff;
    margin: 0 5px;
    background-color: #f0f0f0;
    font-size: 36px;
    font-weight: bold;
    overflow: hidden;
  }

  &__button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 5px;
    transition: background-color 0.3s;

    &--disabled {
      background-color: #cccccc;
    }

    &:hover:not(&--disabled) {
      background-color: #0056b3;
    }
  }

  &__rewards {
    margin-top: 20px;

    &__message {
      color: #28a745;
    }

    &__list {
      list-style-type: none;
      padding: 0;

      &__item {
        margin: 5px 0;
      }
    }
  }
}
